<template>
  <div id="app">
    <Index v-if="showType === 0" @_changeShowType="_changeShowType" @_toggle="_toggle"></Index>
    <Main v-if="showType === 1" @_changeShowType="_changeShowType" @_toggle="_toggle"></Main>
    <End v-if="showType === 2" @_toggle="_toggle"></End>
    <Loading v-if="showType === 9"></Loading>
    <Rule v-show="showRule" @_toggle="_toggle" :tyNum="tyNum" :isHx="isHx"></Rule>
  </div>
</template>

<script>
import Index from './components/Index'
import Main from './components/Main'
import End from './components/End'
import Loading from './components/Loading'
import Rule from './components/Rule'

export default {
  name: 'app',
  components: {
    Index,
    Main,
    End,
    Loading,
    Rule,
  },
  data() {
    return {
      screenWidth: document.body.clientWidth, // 获取设备宽度
      showType: 9, // 显示类型: 0:Index, 1:Main, 2:End
      showRule:false, // 是否显示规则
      tyNum:0, // 汤圆数量
      isHx:false, // 是否核销过
    }
  },
  mounted: function () {
    var that = this;
    let isHx = localStorage.getItem('isHx');
    if(isHx === true) { // 检测是否核销过
      this.showRule = true;
    }
    this.fontFun(640);
    window.addEventListener('resize', function () {
      that.screenWidth = document.body.clientWidth;
    });
    window.setTimeout(function () {
      that.showType = 0;
    },2000);
  },
  methods: {
    fontFun(width) { // 自适应
      var winW = document.documentElement.clientWidth;//浏览器可视宽度
      if(winW >= width) {
        document.documentElement.style.fontSize="625%";
      } else {
        document.documentElement.style.fontSize=(winW / width * 625) + "%";
      }
    },
    _changeShowType (type = 0) {
      this.showType = type;
    },
    _toggle (boolean) {
      this.tyNum = localStorage.getItem('tyNum') || 0;
      this.showRule = boolean;
    }, 
  },
  watch: {
    showRule () {
      this.isHx = localStorage.getItem('isHx') || false;
    }
  },
}
</script>

<style>
*{ 
    margin:0px; 
    padding:0px; 
    border:0px none; 
    outline:none; 
    font-weight: normal;
}
li{ 
    list-style: none;
}
input{ 
    outline:none; 
    border: none;
}
img{ 
    border: 0 none; 
    vertical-align: top;
}
a{ 
    text-decoration: none;
    outline:none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-user-select: none;
    -moz-user-focus: none;
    -moz-user-select: none;
}
em,i,b{ 
    font-style:normal;
}
html{ 
    font-size: 625%;
}
body{ 
    min-width: 320px;
    margin: 0 auto;
    font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Tahoma,Simsun,sans-serif; 
    background: #e50011; 
}
.clearfix:after{
    content: ".";
    display: block; 
    height: 0; 
    clear: both; 
    visibility: hidden;
}
#app {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
}
</style>



