<template>
  <div class="loading">
    <div>
      <div class="loading-ani">
        <i></i>
        <i></i>
        <i></i>
        <i></i>
      </div>
      <em>loading...</em>
    </div>
  </div>
</template>

<script>
export default {
  name: 'loading',
  data () {
    return {
      
    }
  },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.loading{
  display: flex;
  width:100%;
  height:100%;
  justify-content: center;
  align-items:center;
  flex-wrap: wrap;
}
.loading em{
  display: block;
  width: 100%;
  color:#fff;
  font-size:0.8rem;
}
.loading-ani{
  display: flex;
  justify-content: center;
  align-items:center;
}
.loading i{
  display: inline-block;
  width: 0.2rem;
  height: 1rem;
  background: #fff;
  margin:0 0.05rem;
  animation: loading 1s infinite linear;
}
@keyframes loading {
  0%,60%,100% {transform: scale(0.8);}
  30% {transform: scaleY(1.2);}
}
.loading i:nth-child(1){
  animation-delay:0s;
}
.loading i:nth-child(2){
  animation-delay:0.15s;
}
.loading i:nth-child(3){
  animation-delay:0.3s;
}
.loading i:nth-child(4){
  animation-delay:0.45s;
}
</style>



