<template>
  <div class="rule-box" v-show="showRule">
    <div class="rule" >
      <div class="close"><i @click="_toggleRule(false)"></i></div>
      <div class="nav">
        <i @click="_toggle(1)">我的奖品<p class="info-line" v-show="showType == 1"></p></i>
        <em></em>
        <i @click="_toggle(2)">活动说明<p class="info-line" v-show="showType == 2"></p></i>
      </div>
      <div class="info" v-show="showType == 1">
        <div class="mynum">我的汤团：<i>{{ tyNum }}</i></div>
        <div class="list">
          <ul>
            <li>1~9个汤团：精致笔记本、钥匙扣、书签（任选其一）</li>
            <li>10~25个汤团：小火车票一张</li>
            <li>26~30个汤团：旋转木马票一张</li>
            <li>最先答对30题并至客服兑奖可获特等奖：精美宫灯一盏（先到先得，送完即止），答对题请赶紧去客服兑奖哦~</li>
            <li>答对所有题登记手机号码还可参与“抽小金狗手链”活动，3月5日公布中奖名单并由客服联系中奖顾客。</li>
          </ul>
        </div>
        <a href="javascript:void (0);" class="todj" @click="_toggleInput(true)" v-show="isHx == 0">请至客服中心兑奖</a>
        <a href="javascript:void (0);" class="todj" v-show="isHx == 1">您已领奖</a>
      </div>
      <!--  -->
      <div class="info" v-show="showType == 2">
        <div class="sm">
          <strong>活动奖品</strong>
          <i>精致笔记本、钥匙扣、小火车票、旋转木马票、精美宫灯、小金狗手链等丰厚奖品等你来领取</i>
          <strong>活动时间</strong>
          <i>2018.03.02 9:30—2018.03.04 21:30</i>
          <strong>主办单位</strong>
          <i>浙江奥特莱斯广场有限公司</i>
          <strong>活动及兑奖说明</strong>
          <i>当日扫码参与猜灯谜H5小游戏，答对1题即获1个汤圆，由获得汤圆数获取对应奖品，活动期间每个微信号限参与一次，客服核销领取礼品则游戏结束。仅限领取一样，奖品不累加。仅限本人领奖不得代领。</i>
          <i>答对所有题即可参与抽金狗，活动结束从客服联系中奖顾客。</i>
        </div>
      </div>
    </div>
    <div class="inputarea" v-show="inputShow">
      <div class="inner">
        <input type="text" placeholder="请输入核销码" v-model="code">
        <span>
          <i @click="_toggleInput(false)">关闭</i>
          <i @click="_sendCode">确定</i>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Rule',
  props: {tyNum:{
    default: 0
  },isHx:{
    default:false
  }},
  data () {
    return {
      showRule:false, // 是否显示规则
      inputShow:false, // 是否显示输入核销码的区域
      code: '', // 核销码
      trueCode: '123456', // 定义的核销码
      showType:1, // 左右切换
    }
  },
  computed:{
    rttyNum () {
      return this.tyNum;
    }
  },
  methods: {
    _toggle (val) {
      this.showType = val;
    },
    _toggleRule (boolean) {
      this.showRule = boolean;
      this.$emit('_toggle', boolean);
    },
    _toggleInput (boolean) {
      this.inputShow = boolean;
    },
    _sendCode () {
      let code = this.code.trim();
      if(this.trueCode == code) {
        localStorage.setItem('isHx', 1);
        this.isHx = 1;
        alert('核销完成');
        this.inputShow = false;
      } else {
        alert('核销码不正确');
      }
      
    } 
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.rule{
  width: 100%;
  height:100%;
  position:absolute;
  top: 0;
  left:0;
  bottom:0;
  right:0;
  background: rgba(0,0,0,0.8);
  font-size:0.3rem;
  color:#fff;
  overflow-y: auto;
  z-index:9999;
}
.close{
  overflow:hidden;
}
.close i{
  display:block;
  width: 1.2rem;
  height:1.2rem;
  background: url('../assets/close.png') center center no-repeat;
  float:right;
}
.mynum{
  height: 1rem;
  line-height: 1rem;
  color:#fff;
  text-align:center;
}
.mynum i{
  font-weight: bold;
  
}
.list{
  border:1px solid #fff;
  max-width: 70%;
  min-width:20%;
  max-height:4rem;
  margin:0 auto;
  padding:0.5rem;
  overflow-y:auto;
}
.list li{
  /*height: 0.8rem;*/
  line-height: 0.8rem;
}
.list li i{
  float:left;
}
.list li em{
  float:right;
}
.todj{
  display: block;
  width: 3rem;
  height: 0.6rem;
  line-height:0.6rem;
  border-radius:0.5rem;
  border:1px solid #fff;
  text-align:center;
  color:#fff;
  margin: 1rem auto 0;
}
.inputarea{
  position: absolute;
  top: 0;
  left: 0;
  bottom:0;
  right:0;
  width: 100%;
  background: url('../assets/main-bg1.jpg') top left no-repeat, url('../assets/main-bg2.jpg') bottom left no-repeat rgba(229,0,17,1);
  background-size:contain;
  color:#000;
  font-size:0.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index:10000;
}
.inputarea .inner{
  width: 100%;
}
.inputarea input{
  display: block;
  border:1px solid #fff;
  width: 60%;
  height: 0.6rem;
  border-radius:0.5rem;
  margin:0 auto;
  padding:0.2rem 0.5rem;
  font-size:0.3rem;
}
.inputarea span{
  display: block;
  margin:0 auto;
  text-align: center;
  margin-top:0.3rem;
}
.inputarea i{
  display:inline-block;
  width: 2rem;
  height: 0.8rem;
  line-height:0.8rem;
  text-align:center;
  border:1px solid #fff;
  margin:0 0.3rem;
  color:#fff;
  border-radius:0.4rem;
}
.nav{
  height:0.8rem;
  width: 100%;
  text-align: center;
  line-height: 0.8rem;
  border-bottom:1px solid #fff;
  position:relative;
}
.nav i{
  display: inline-block;
  height: 0.8rem;
  line-height:0.8rem;
  
}
.nav em{
  display: inline-block;
  height: 0.5rem;
  width: 0;
  border-right:1px solid #fff;
  vertical-align: middle;
  margin:0 0.2rem;
}
.nav .info-line{
  display: block;
  width: 1rem;
  height:0.1rem;
  background: #fff;
  margin:-0.1rem auto 0;
}
.sm{
  width: 90%;
  margin:0 auto;
  max-height: 6.5rem;
  overflow-y: auto;
  padding:0.3rem 0;
}
.sm strong,.sm i{
  display: block;
  line-height: 0.5rem;
}
.sm strong{
  display: inline-block;
  height: 0.6rem;
  line-height: 0.6rem;
  margin:0.2rem 0;
  color:#000;
  background: #fff;
  padding:0 0.2rem;
}
</style>



