<template>
  <div class="index">
    <!-- <div class="contain">
      <div class="text"></div>
    </div>
    <div class="title"></div> -->
    <i class="package" @click="_toggleRule(true)"></i>
    <a href="javascript:void (0);" class="start" @click="_enterNent(1)"></a>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      msg: 'Index',
      showRule:false, // 是否显示规则
      inputShow:false, // 是否显示输入核销码的区域
      code: '', // 核销码
    }
  },
  methods: {
    _enterNent(step,){
      this.$emit('_changeShowType', step,)
    },
    _toggleRule (boolean) {
      this.$emit('_toggle',boolean);
    },
    _toggleInput (boolean) {
      this.inputShow = boolean;
    },
    _sendCode () {
      let code = this.code;
      console.log(code);
      this.inputShow = false;
    } 
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.index{
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right:0;
  background: url('../assets/index-bg-bottom.png') bottom center no-repeat,url('../assets/index-bg-top.png') top center no-repeat ;
  background-size:contain;
}
.package{
  display:block;
  width: 1.4rem;
  height: 1.7rem;
  background: url('../assets/package.png') center center no-repeat;
  background-size:contain;
  position: absolute;
  top: 1rem;
  right:0.1rem;
  animation: clickme 2s infinite ease-in;
}
.start{
  display:block;
  width: 3.18rem;
  height: 1.75rem;
  background: url('../assets/start-btn.png') center center no-repeat;
  background-size:contain;
  position:absolute;
  bottom:15%;
  left: 50%;
  margin-left: -1.59rem;
}
@keyframes clickme {
  0%,100%{transform: scale(1)}
  50%{transform:scale(1.2)}
}
.rule{
  width: 100%;
  height:100%;
  position:absolute;
  top: 0;
  left:0;
  bottom:0;
  right:0;
  background: rgba(0,0,0,0.8);
  font-size:0.3rem;
  color:#fff;
}
.close{
  overflow:hidden;
}
.close i{
  display:block;
  width: 1.2rem;
  height:1.2rem;
  background: url('../assets/close.png') center center no-repeat;
  float:right;
}
.mynum{
  height: 1rem;
  line-height: 1rem;
  color:#fff;
  text-align:center;
}
.mynum i{
  font-weight: bold;
  
}
.list{
  border:1px solid #fff;
  max-width: 70%;
  min-width:20%;
  margin:0 auto;
  padding:0.5rem;
  overflow-y:auto;
}
.list li{
  height: 0.8rem;
  line-height: 0.8rem;
}
.list li i{
  float:left;
}
.list li em{
  float:right;
}
.todj{
  display: block;
  width: 3rem;
  height: 0.6rem;
  line-height:0.6rem;
  border-radius:0.5rem;
  border:1px solid #fff;
  text-align:center;
  color:#fff;
  margin: 1rem auto 0;
}
.inputarea{
  position: absolute;
  top: 0;
  left: 0;
  bottom:0;
  right:0;
  width: 100%;
  background: #e50011;
  color:#000;
  font-size:0.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inputarea .inner{
  width: 100%;
}
.inputarea input{
  display: block;
  border:1px solid #fff;
  width: 60%;
  height: 0.6rem;
  border-radius:0.5rem;
  margin:0 auto;
  padding:0.2rem 0.5rem;
  font-size:0.3rem;
}
.inputarea span{
  display: block;
  margin:0 auto;
  text-align: center;
  margin-top:0.3rem;
}
.inputarea i{
  display:inline-block;
  width: 2rem;
  height: 0.8rem;
  line-height:0.8rem;
  text-align:center;
  border:1px solid #fff;
  margin:0 0.3rem;
  color:#fff;
  border-radius:0.4rem;
}
</style>



