<template>
  <div class="end">
    <div class="contain">
      <div class="inner">
        <i>您已答完所有问题</i>
        <i>快去A237客服中心领奖吧</i>
        <em class="btn1" @click="_toggleRule(true)"></em>
        <i>输入手机号参与抽奖</i>
        <i>赢取狗年定制黄金手链</i>
        <i>活动结束后开奖</i>
        <div>
          <input type="number" class="inputtel" placeholder="请输入手机号码" v-model="tel">
        </div>
        <em class="submit" @click="_checkTel()"></em>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'End',
  data () {
    return {
      msg: 'End',
      tel:'', // 联系电话
    }
  },
  methods: {
    _checkTel () { // 检查手机号码是否合法
      const tel = this.tel.trim(); 
      if(!(/^1[34578]\d{9}$/.test(tel))){
        alert('手机号码有误，请重新填写');
      } else {
        this._submitTel(tel);
      }
    },
    _submitTel (tel) {
      const tyNum = localStorage.getItem('tyNum') || 0;
      this.$axios({
        url:`/tel?tel=${tel}&tyNum=${tyNum}`, 
        method: 'get'
      })
      .then(function (res){
        alert('提交成功');
      })
      .catch(function (err) {
        alert(err);
      });
    },
    _toggleRule (boolean) {
      this.$emit('_toggle',boolean);
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.end{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  background:url('../assets/end-top.jpg') top left no-repeat, url('../assets/end-bottom.jpg') bottom left no-repeat;
  background-size:contain;
}
.contain{
  width: 5.82rem;
  height: 6.45rem;
  background:url('../assets/end-main.jpg') center center no-repeat;
  background-size:contain;
  position: absolute;
  top: 50%;
  left:50%;
  margin: -2.9rem 0 0 -2.96rem;
  font-size:0.3rem;
  color:#fdf5cb;
  text-align: center;
}
.contain .inner{
  width: 4rem;
  margin: 0.5rem auto 0;
}
.contain .inner i{
  display: block;
  height: 0.5rem;
  line-height:0.5rem;
}
.contain .inner .btn1{
  display: block;
  width: 2.16rem;
  height:0.51rem;
  background: url('../assets/end-btn1.png') center center no-repeat;
  background-size: contain;
  margin:0.1rem auto 0.4rem;
}
.contain .inner .inputtel{
  margin:0.1rem auto 0;
  width: 3rem;
  height:0.5rem;
  padding:0.05rem 0.2rem;
  background: #fdf5cb;
  border-radius:0.2rem;
}
.submit{
  display: inline-block;
  width:1.09rem;
  height:0.51rem;
  background: url('../assets/end-btn2.png') center center no-repeat;
  background-size:contain;
  margin-top:0.2rem;
}
</style>



