<template>
  <div class="main">
    <i class="package" @click="_toggleRule(true)"></i>
    <div class="subject" @click="_share()">
      答错不能改哦，求助一下朋友
      <i></i>
    </div>
    <div class="choose_area">
      <div class="title" :class="qxclass">
        <i v-for="item in subject.qxnamelist">{{ item.name }}</i>
      </div>
      <div class="tip">打一字</div>
      <div class="choose">
        <div class="choose_item" v-for="(item,index) in subject.lists[subject.num.nownum].option" @click="_chooseItem(index)">
          <i>
            <b :class="{cfalse:subject.list.userChoose == index, ctrue:subject.list.answer == index}" ></b>
            <!-- v-show="subject.list.userChooseItem == index" -->
          </i>
          <em>{{ item }}</em>
        </div>
      </div>
    </div>
    <a href="javascript:void(0);" @click="_enterNent()" class="next"></a>
  </div>
</template>

<script>
import Vue from 'vue'
import dataList from '../data/data.json'
export default {
  name: 'Main',
  props:['schedule'],
  data () {
    return {
      msg: 'Main',
      subject: {
        // type:2, // 答题类型:1:未答题;2:答对; 3:答错; 4: 答题结束,
        isOver: false, // 是否答题结束
        num:{
          nownum: 0, // 当前题目编号0-max
          allnum: 29, // 总的题目数量0-max
        }, 
        list:{ // 当前题目数据
          id: 1, // 题目id ,范围:1-max
          answer: 0, // 题目正确答案 ,范围： 0-3
          userChooseIsTrue:-1, // 用户选择的答案
          userChooseItem: -1, // 
          canChoose:true, // 用户是否能答题
        },
        lists:dataList.lists,
        qxnamelist:[],
      },
      qxclass:'',
    }
  },
  watch: {
    'subject.isOver' () {
      if(this.subject.isOver) {
        this.$emit('_changeShowType', 2);
      }
      
    }
  },
  mounted () {
    this._initAns();
    
    // 获取/保存 上次答题题目编号
    var savenum = localStorage.getItem('savenum');
    if(savenum) {
      // this.subject.isOver = savenum == -1 ? true : false;
      // this.subject.num.nownum = savenum;
      if(savenum == -1) {
        this.subject.isOver = true;
      } else {
        this.subject.num.nownum = savenum;
        this._qxname(12,2,0);
      }
    } else {
      this._qxname(12,2,0);
    }
    this.subject.isOver = this.subject.num.nownum >= this.subject.num.allnum ? true : false;
  },
  methods: {
    _qxname (rot, ctop, num) {
      this.qxnamelist = [];
      let arrayFlag = [];
      let name = this.subject.lists[+this.subject.num.nownum+num].name.split("");
      const len = name.length;
      this.qxclass = `style${len}`;
      name.forEach((item, index) => {
        
        let transform = `rotate(${(index + 0.5 - len / 2) * rot}deg);`;
        let top =  `${(Math.abs(index - len / 2) * Math.abs(index - len / 2 + 1) - len)*ctop}px;`;
        arrayFlag.push({name: item});
      });
      this.subject.qxnamelist = arrayFlag;  
      console.log(this.qxnamelist);   
      console.log(arrayFlag); 
    },
    _share () {
      window.location.href = `http://alonelyer.com/share/share.html?id=${this.subject.num.nownum}`;
    },
    _initAns () {
      this.subject.list.userChoose = -1;
      this.subject.list.answer = -1;
    },
    _enterNent (){ // 下一步
      this._initAns();
      
      this.subject.list.canChoose = true;
      this.subject.isOver = this.subject.num.nownum >= this.subject.num.allnum ? true : false;
      if(this.subject.isOver == false){
        this._qxname(12,2,1);
        this.subject.num.nownum++
        this._writeAns(0);
      } else {
        this.$emit('_changeShowType', 2);
      }
    },
    _getAns (id) { // 获取答案
      this.subject.list.answer = +this.subject.lists[this.subject.num.nownum].trueAnswer-1;
    },
    _addTyNum () { // 增加汤圆
      var tyNum = localStorage.getItem('tyNum');
      if(tyNum) {
        localStorage.setItem('tyNum', +tyNum+1);
      } else {
        localStorage.setItem('tyNum', 1);
      }
    },
    _writeAns (count=1) { // 保存答题进度
      if(this.subject.isOver == false){
        localStorage.setItem('savenum', +this.subject.num.nownum+count);
      } else {
        localStorage.setItem('savenum', -1);
      }
    },
    _chooseItem (item) { // 选择答案
      if(this.subject.list.canChoose) {
        //发送给后台用户选择的答案，后台返回给我正确答案
        this.subject.list.canChoose = false;
        this.subject.list.userChoose = item;
        this.subject.list.userChooseItem = item;
        this._getAns();
        this._writeAns(1);
        if(this.subject.list.answer == item) { // 答对
          this._addTyNum();
        } else { // 答错
          console.log('wrong');
        }
        
      } 
    },
    _toggleRule (boolean) {
      this.$emit('_toggle',boolean);
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main{
  width: 100%;
  height:100%;
  background: url('../assets/main-bg1.jpg') top right no-repeat, url('../assets/main-bg2.jpg') bottom left no-repeat;
  background-size:contain;
  font-family: "宋体";
}
.subject{
  width: 100%;
  font-size:0.3rem;
  color:#fefbd0;
  text-align: center;
  height: 0.5rem;
  line-height: 0.5rem;
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top:-3rem;
}
.subject i{
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  background: url('../assets/main-help.png') center center no-repeat;
  background-size:contain;
  margin-left:0.1rem;
  animation: clickme 1s infinite ease-in;
}
.next{
  display: block;
  width: 1.8rem;
  height: 1.31rem;
  background: url('../assets/main-next.png') center center no-repeat;
  background-size:contain;
  position: absolute;
  bottom: 50%;
  left:50%;
  margin-left: -0.9rem;
  margin-bottom:-70%;
}
.choose_area{
  width: 4.88rem;
  height: 4.26rem;
  background: url('../assets/main.png') center center no-repeat;
  background-size:contain;
  margin: 0 auto;
  position: absolute;
  top: 30%;
  left:50%;
  margin-left:-2.44rem;
}
.choose_area .title{
  position: relative;
  font-size:0.6rem;
  color:#fefbd0;
  width: 100%;
  height: 1.1rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}
.choose_area .title i{
  position:relative;
}
.choose_area .tip{
  width: 100%;
  color:#fefbd0;
  font-size:0.25rem;
  text-align: center;
  height: 0.4rem;
  line-height:0.4rem;
}
.choose{
  width: 100%;
  height: 2.76rem;
  display: flex;
  flex-wrap:wrap;
}
.choose_area .choose .choose_item{
  width: 50%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.choose_area .choose .choose_item i,.choose_area .choose .choose_item em{
  display: block;
}
.choose_area .choose .choose_item i{
  width: 0.35rem;
  height: 0.35rem;
  border: 1px solid #fefbd0;
  border-radius: 0.05rem;
  margin-right: 0.1rem;
  position:relative;
}
.choose_area .choose .choose_item em{
  font-size:0.65rem;
  color:#fefbd0;
}

.choose_area .choose .choose_item .cfalse{
  display: inline-block;
  position:absolute;
  bottom:-0.08rem;
  left:-0.08rem;
  background: url('../assets/wrong.png') center center no-repeat;
  background-size: contain;
  width: 0.5rem;
  height: 0.5rem;
}
.choose_area .choose .choose_item .ctrue{
  display: inline-block;
  position:absolute;
  bottom:0;
  left:0;
  background: url('../assets/right.png') center center no-repeat!important;
  background-size: contain;
  width: 0.5rem;
  height: 0.5rem;
}
@keyframes clickme {
  0%,100%{transform: scale(1)}
  50%{transform:scale(1.2)}
}
.style1 i{

}
.style2 i:nth-child(1){
  transform: rotate(-8deg); top: -1px;
}
.style2 i:nth-child(2){
  transform: rotate(8deg); top: -1px;
}

.style3 i:nth-child(1){
  transform: rotate(-10deg); top: 1px;
}
.style3 i:nth-child(2){
  top:-3px;
}
.style3 i:nth-child(3){
  transform: rotate(10deg); top: 1px;
}


.style4 i:nth-child(1){
  transform: rotate(-20deg); top: 4px;
}
.style4 i:nth-child(2){
  transform: rotate(-8deg); top: -3px;
}
.style4 i:nth-child(3){
  transform: rotate(8deg); top: -3px;
}
.style4 i:nth-child(4){
  transform: rotate(20deg); top: 4px;
}

.style5 i:nth-child(1){
  transform: rotate(-25deg); top: 10px;
}
.style5 i:nth-child(2){
  transform: rotate(-15deg); top: 1px;
}
.style5 i:nth-child(3){
  top:-3px;
}
.style5 i:nth-child(4){
  transform: rotate(15deg); top: 1px;
}
.style5 i:nth-child(5){
  transform: rotate(25deg); top: 10px;
}
.package{
  display:block;
  width: 1rem;
  height: 1.4rem;
  background: url('../assets/package.png') center center no-repeat;
  background-size:contain;
  position: absolute;
  top: 1rem;
  right:0.1rem;
  animation: clickme 2s infinite ease-in;
  z-index:20;
}
</style>



